<template>
  <div class="red_box">
    <div class="red_nav">
      <div class="red_nav_btn" @click="goBack">
        <i class="el-icon-back"></i>
      </div>
      <div class="red_nav_title">{{$t('Create Red Envelope')}}</div>
      <div class="red_nav_text">
        <!-- <router-link to="/redlist">我塞的红包</router-link> -->
        <router-link to="/">
          <i class="el-icon-s-home"></i>
        </router-link>
      </div>
    </div>
    <div class="red_tab_box">
      <div
        class="red_tab_item"
        @click="navCheck(1)"
        :class="nav == 1 ? 'red_tab_active' : ''"
      >{{$t('Fix Amount')}}</div>
      <div
        class="red_tab_item"
        @click="navCheck(2)"
        :class="nav == 2 ? 'red_tab_active' : ''"
      >{{$t('Random Amount')}}</div>
      <div
        class="red_tab_item"
        @click="navCheck(3)"
        :class="nav == 3 ? 'red_tab_active' : ''"
      >{{$t('Account Creation')}}</div>
      <div class="red_tab_line" :class="line_style"></div>
    </div>
    <div class="red_content_box">
      <template v-if="nav != 3">
        <!-- <div class="red_content_item">
          <div>币种</div>
          <div>YAS</div>
        </div>-->
        <div class="red_content_item red_input">
          <div class="red_content_input_text">{{$t('Total')}}</div>
          <div class="red_content_input">
            <input type="number" v-model="enuNum" :placeholder="$t('Min')">
            <span>YAS</span>
          </div>
        </div>
      </template>
      <template v-else>
        <div class="red_content_item red_input">
          <div class="red_content_input_text">{{$t('Amount')}}</div>
          <div class="red_content_input">
            <input type="number" v-model="enuNum">
            <span>YAS</span>
          </div>
        </div>
      </template>
      <div class="red_content_item red_input">
        <div class="red_content_input_text">{{$t('Quantity')}}</div>
        <div class="red_content_input">
          <input type="number" v-model="packetNum" :placeholder="$t('Max')">
          <!-- <span>{{$t('Number')}}</span> -->
        </div>
      </div>
      <div class="red_content_item red_textarea">
        <textarea v-model="memo" :placeholder="$t('Best wishes')"></textarea>
      </div>
      <div class="red_warn_item">
        <span data-v-4ed57b8a>{{$t('RedPacketMessage')}}</span>
        <router-link to="/redabout">
          <i class="el-icon-question" aria-hidden="true"></i>
        </router-link>
      </div>
      <div class="red_btn_item">
        <button @click="sendTransaction">{{$t('Transfer YAS to Red Envelope')}}</button>
      </div>
    </div>
    <div class="red_foot">
      <small>{{$t('RedPacketMessageOther')}}</small>
    </div>
    <loading :loading-status="loading"></loading>
    <message :message-status="messageStatus" :message-text="messageText"></message>
  </div>
</template>

<script>
const httpNetwork = {
  blockchain: "eos",
  chainId: "ed8636abfe625d99fc9a759d49a016fd8dcae9193676a020aae2540c9fffe32f"
};

const eosOptions = {
  broadcast: true,
  sign: true,
  chainId: "ed8636abfe625d99fc9a759d49a016fd8dcae9193676a020aae2540c9fffe32f"
};

export default {
  components: {
    loading: resolve => {
      require(["@/components/loading.vue"], resolve);
    },
    message: resolve => {
      require(["@/components/message.vue"], resolve);
    }
  },
  data() {
    return {
      loading: false,
      messageStatus: false,
      messageText: "",
      nav: 1,
      line_style: "line_style_1",
      enuNum: "",
      packetNum: "",
      memo: ""
    };
  },
  created() {},

  methods: {
    navCheck(num) {
      this.nav = num;
      this.line_style = "line_style_" + num;
      if (num == 3) {
        this.enuNum = 1;
      } else {
        this.enuNum = "";
      }
      this.packetNum = "";
      this.memo = "";
    },
    goBack() {
      window.history.go(-1);
    },
    // 点击发送交易
    async sendTransaction() {
      if (this.enuNum < 0.1) {
        this.$message.error(this.$t("RedMoneyMessage"));
      } else if (this.packetNum > 1000 || this.packetNum == "") {
        this.$message.error(this.$t("RedNumMessage"));
      } else {
        this.loading = true;
        //get keypair
        let pair = await this.chainApi.generateKeyPair();
        let privateKey = pair.privateKey;
        let publicKey = pair.publicKey;
        console.log("publicKey:", publicKey);

        let fl = this.chainApi.cutKey(privateKey);
        let f = fl.f;
        let l = fl.l;
        console.log(f, l);

        //get type
        let type = this.nav == 2 ? 2 : 1;
        console.log("type:", type);

        //get id
        let id = Date.now();
        console.log("id:", id);

        //get number
        let number = new Number(this.packetNum);
        console.log("number:", number);

        //get word
        let word = this.memo;
        if (word == "") {
          if (localStorage.getItem("Lang") == "en") {
            word = "Best wishes!";
          } else {
            word = "恭喜发财，大吉大利！";
          }
        }
        let _memo =
          type + "|" + id + "|" + number + "|" + word + "|" + publicKey;
        console.log("memo:", _memo);

        //get enu amount
        let amount = new Number(this.enuNum);
        if (this.nav == 3) {
          amount = amount * number;
        }
        let asset = amount.toFixed(4) + " YAS";
        console.log("asset:", asset);

        let sender = await this.chainApi.connectIdentity();
        try {
          let res = await this.chainApi.sendEnvelope(sender, asset, _memo);
          this.loading = false;
          console.log(res);
          if (res.transaction_id) {
            this.$message.success(this.$t("TranSuccess"));
            this.$router.push({
              path: "detail?id=" + id + "&f=" + f + "&l=" + l
            });
          }
          /* this.$message.success(
            JSON.stringify({
              id: id,
              key: privateKey
            })
          );*/
        } catch (err) {
          this.loading = false;
          console.log(err);
          this.$message.error(this.$t("TranFail"));
        }
      }
    }
  },
  computed: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;
.red_box {
  width: 100%;
  height: 100%;
  .red_nav {
    width: 100%;
    height: 44 * @rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15 * @rem;
    background: #ce2344;
    box-sizing: border-box;
    .red_nav_btn {
      width: 80 * @rem;
      height: 44 * @rem;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      i {
        color: #fff;
        font-size: 25 * @rem;
      }
    }
    .red_nav_title {
      width: 120 * @rem;
      font-size: 12 * @rem;
      line-height: 44 * @rem;
      color: #fff;
      text-align: center;
    }
    .red_nav_text {
      width: 80 * @rem;
      height: 44 * @rem;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      a {
        font-size: 23 * @rem;
        color: #fff;
      }
    }
  }
  .red_tab_box {
    width: 100%;
    height: 40 * @rem;
    background: #f8f8f8;
    position: relative;
    display: flex;
    .red_tab_item {
      font-size: 12 * @rem;
      color: #a69987;
      padding: 0 8 * @rem;
      height: 40 * @rem;
      line-height: 40 * @rem;
    }
    .red_tab_active {
      color: #5d4220;
    }
    .red_tab_line {
      height: 2 * @rem;
      background-color: #5d4220;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;
    }
    .line_style_1 {
      width: 40 * @rem;
      transform: translateX(12 * @rem);
      transition: transform 0.3s;
    }
    .line_style_2 {
      width: 50 * @rem;
      transform: translateX(78 * @rem);
      transition: transform 0.3s;
    }
    .line_style_3 {
      width: 50 * @rem;
      transform: translateX(152 * @rem);
      transition: transform 0.3s;
    }
  }
  .red_content_box {
    width: 100%;
    padding: 24 * @rem 16 * @rem;
    box-sizing: border-box;
    .red_content_item {
      display: flex;
      background-color: #f8f8f8;
      margin-bottom: 20 * @rem;
      padding: 16 * @rem;
      font-size: 12 * @rem;
      justify-content: space-between;
      align-items: center;
      color: #2c3e50;
    }
    .red_input {
      .red_content_input_text {
        color: #5d4220;
      }
      .red_content_input {
        display: flex;
        align-items: center;
        color: #5d4220;
        input {
          border-radius: 4 * @rem;
          border: none;
          box-sizing: border-box;
          color: #606266;
          display: inline-block;
          font-size: inherit;
          height: 100%;
          outline: none;
          padding: 0 10 * @rem 0 15 * @rem;
          background-color: #f8f8f8;
          text-align: right;
          ime-mode: disabled;
          font-size: 14 * @rem;
        }
      }
    }
    .red_textarea {
      margin-top: 24 * @rem;
      background-color: #f8f8f8;
      font-size: 16 * @rem;
      height: 48 * @rem;
      padding: 16 * @rem;
      border-radius: 3 * @rem;
      textarea {
        background-color: #f8f8f8;
        border: none;
        width: 100%;
        height: 100%;
        outline: none;
        resize: none;
        font-size: 14 * @rem;
      }
    }
    .red_warn_item {
      display: flex;
      margin-top: 4 * @rem;
      padding: 0 3 * @rem;
      justify-content: space-between;
      align-items: center;
      color: #5d4220;
      box-sizing: border-box;
      i {
        font-size: 20 * @rem;
        color: #5d4220;
      }
    }
    .red_btn_item {
      margin-top: 70 * @rem;
      button {
        background-color: #fcdbb2;
        width: 100%;
        padding: 14 * @rem 0;
        text-align: center;
        border: none;
        outline: none;
        font-size: 14 * @rem;
        color: #5d4220;
        border-radius: 2 * @rem;
      }
    }
  }
  .red_foot {
    bottom: 16 * @rem;
    text-align: center;
    width: 100%;
    font-size: 14 * @rem;
    color: #c9c2b7;
  }
}
</style>



